import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
//import {tokenNotExpired} from 'angular-jwt';


@Injectable()
export class ExamService {
  constructor(private http: Http) { }
 
//getting exams
  getExams(){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    //let ep = 'http://localhost:3000/exams/examnames';
    let ep = 'http://localhost:3000/exams/examnames';
    return this.http.get(ep,{ headers: headers})
      .map(res => res.json());
  }

  //adding exam name
  addExam(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/addexam';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }
  //update exam
  updateExam(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/updateexam';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }
    //update question
  updateQues(ques){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/updateQues';
    return this.http.post(ep,ques,{ headers: headers})
      .map(res => res.json());
  }

  //deleting exam
  deleteExam(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/deleteexam';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }

  //adding question to particular exam
addExamQuestion(examQuestion){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/addquestion';
    return this.http.post(ep,examQuestion,{ headers: headers})
      .map(res => res.json());
  }

//deleting question to particular exam
deleteExamQuestion(examQuestion){
  let headers = new Headers();
  headers.append('Content-Type', 'application/json');
  let ep = 'http://localhost:3000/exams/deletequestion';
  return this.http.post(ep,examQuestion,{ headers: headers})
    .map(res => res.json());
}
  //getting all questions of particular exam
  getExamQuestion(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/getquestions';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }
  
  //getting number of question of particular exam questions
  getExamQuestionStat(exam){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/exams/examquesstat';
    return this.http.post(ep,exam,{ headers: headers})
      .map(res => res.json());
  }

  //getting number of question of particular exam questions
  addResult(result){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let ep = 'http://localhost:3000/result/addResult';
    return this.http.post(ep,result,{ headers: headers})
      .map(res => res.json());
  }
  getNativeWindow() {
    return window;
}

}



