import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ResultService {

  //answers
  public aptituteAns=[];
  resoningAns=[];
  englisgAns=[];
  genralAns=[];
  computerAns=[];

  //option selected
  selectedA=[];
  selectedE=[];
  selectedR=[];
  selectedG=[];
  selectedC=[];

  constructor(private http: Http) { }

  setAptiAns(aptiAns){
    this.aptituteAns.push(aptiAns);
  }

  setResoningAns(rAns){
    this.resoningAns.push(rAns);
  }
  setEnglishAns(eAns){
    this.englisgAns.push(eAns);
  }

  setGenralAns(gAns){
    this.genralAns.push(gAns);
  }

  setComputerAns(cAns){
    this.computerAns.push(cAns);
  }

 setAOption(aopt){
   this.selectedA = aopt;
 }

 setROption(ropt){
   this.selectedR = ropt;
 }

 setEOption(eopt){
   this.selectedE = eopt;
 }

 setGOption(gopt){
   this.selectedG = gopt;
 }

 setCOption(copt){
   this.selectedC = copt;
 }

 clearAll(){
   this.aptituteAns=[];
   this.computerAns=[];
   this.englisgAns=[];
   this.resoningAns=[];
   this.genralAns=[];
 }

 //getting exams
 getResults(examname){
  let headers = new Headers();
  headers.append('Content-Type','application/json');
  //let ep = 'http://localhost:3000/exams/examnames';
  let ep = 'http://localhost:3000/result/getresults';
  return this.http.post(ep,examname,{ headers: headers})
    .map(res => res.json());
}

 
}



