import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-examintro',
  templateUrl: './examintro.component.html',
  styleUrls: ['./examintro.component.css']
})
export class ExamintroComponent implements OnInit {

  Exam_name;
  constructor(
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    let user = localStorage.getItem('user');
    let data = JSON.parse(user);
    this.Exam_name= data.exam;
    console.log(this.Exam_name);
  }

  startExam(){
    this.router.navigate(['examboard/mainpage',this.Exam_name]);
  }
}



