import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  name: String;
  username: String;
  email: String;
  password: String;
  cpassword: String;

  femail:string;
  set:boolean;
  send:boolean;

  lbox:Boolean;
  //msg
  errormsg:String;
  successmsg:String;
  progress:Boolean;
  constructor(private authService: AuthService,
    private router: Router) { }

  ngOnInit() {
    this.send = true;
  }

  //login
  loginUser() {
    this.errormsg ='';
    const user = {
      email: this.email,
      password: this.password,
    };
      if(user.email == undefined || user.email == '' ){
        this.errormsg = "Please fill email-id";
        //this.progress = false;
        return false;     
       }
       if(user.password == undefined || user.password == ''){
        this.errormsg = "Please fill password";
        //this.progress = false;
        return false; 
       }
    // authenmticate user 
    this.authService.authenticateUser(user).subscribe(data => {
      if (data.success) {
        this.authService.storeUserData(data.token,data.user);
        this.router.navigate(['/examboard']);
      } else {
        //this.router.navigate(['/']);
        this.errormsg = data.msg;
       // this.progress = false;
        return false;;
      }
    });
  }

  closeAll(){
    this.successmsg = '';
    this.errormsg = '';
    this.name = '';
    this.email = '';
    this.password = '';
    this.cpassword = '';
  }

  lBox(){
    this.lbox=true;
  }

  sendLink(){
    this.closeAll();
    this.set = true;
    if(this.femail == undefined || this.femail == ''){
      this.errormsg = "Please Provide your email address";
    }
    else{
      let data = {
        email : this.femail
      }
      this.authService.resetPass(data).subscribe(data => {
        if (data.success) {
          this.set = false;
          this.send = false;
          this.successmsg = data.msg;
        } else {
          this.set = false;
          this.errormsg = data.msg;
          return false;;
        }
      });
    }
  }

  resend(){
    this.successmsg = '';
    this.sendLink();
  }

  resetFemail(){
    this.femail = '';
  }
}



