import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {ExamService} from '../../../services/exam.service';
import {TimerObservable} from "rxjs/observable/TimerObservable";
import {Subscription} from "rxjs";
import { ResultService } from '../../../services/result.service';
@Component({
  selector: 'app-examdash',
  templateUrl: './examdash.component.html',
  styleUrls: ['./examdash.component.css']
})
export class ExamdashComponent implements OnInit {
  //user info
  username;
  gender;
  age=20;
//selection not working
  ticks=0;
  hr='';
  sec = '';
  min='';
  time;
  endtime;
  stop:boolean = false;
  private subscription: Subscription;

  examName;
 
  selectedA=[];
  selectedE=[];
  selectedR=[];
  selectedG=[];
  selectedC=[];
   //questions
  aptituteQues=[];
  resoningQuestion=[];
  englisgQuestion=[];
  genralQuestion=[];
  computerQuestion=[];

  //flags
  apti:boolean;
  res:boolean;
  eng:boolean;
  gen:boolean;
  comp:boolean;

  //lengths
  aptiLen=0;
  resLen=0;
  engLen=0;
  genLen=0;
  compLen=0;

  //=============//
  value:number=0;
  next:boolean = true;
  //============//

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private es:ExamService,
    private rs:ResultService
  ) {
    //setting user info
    let user      = localStorage.getItem('user');
    let data      = JSON.parse(user);
    this.username = data.name;
    this.gender = data.gender;

    this.examName = this.route.snapshot.params['id'];
    if(this.examName == undefined || this.examName == '')
          this.router.navigate(['/examdash']);
   else{
    const ename = {
        examname : this.examName
       };
     this.es.getExamQuestion(ename).subscribe(data => {
     if (data.success) {

      data.data.forEach(elmt => {
        if(elmt.category=="aptitute") {this.aptituteQues.push(elmt); 
          this.rs.setAptiAns(elmt.answer);
        }
        else if(elmt.category=="resoning") {this.resoningQuestion.push(elmt);this.rs.setResoningAns(elmt.answer);}
        else if(elmt.category=="english") {this.englisgQuestion.push(elmt);this.rs.setEnglishAns(elmt.answer);}
        else if(elmt.category=="genralawareness") {this.genralQuestion.push(elmt);this.rs.setGenralAns(elmt.answer);}
        else if(elmt.category=="computer") {this.computerQuestion.push(elmt);this.rs.setComputerAns(elmt.answer); } 
      });
      
      for(let i = 0;i< this.aptituteQues.length;i++)
           this.selectedA.push({ qno:i,no:null, opt:null,status:""});

      for(let i = 0;i< this.resoningQuestion.length;i++)
           this.selectedR.push({ qno:i,no:null, opt:null,status:""});
           
      for(let i = 0;i< this.englisgQuestion.length;i++)
           this.selectedE.push({ qno:i,no:null, opt:null,status:""});

      for(let i = 0;i< this.genralQuestion.length;i++)
           this.selectedG.push({ qno:i,no:null, opt:null,status:""});

      for(let i = 0;i< this.computerQuestion.length;i++)
           this.selectedC.push({ qno:i,no:null, opt:null,status:""});  

       } else this.router.navigate(['/examdash']);
    });
   }
  }

  ngOnInit() {
    //timer
    this.endtime = new Date(Date.parse(new Date().toString()) +  2 * 60 * 60 * 1000);
    this.timer();
    this.apti=true;  
 }

 ngOnDestroy() {
  this.subscription.unsubscribe();
}

addApti(val,stat){
      this.selectedA[this.aptiLen].no=this.aptiLen+1;
      this.selectedA[this.aptiLen].opt=val;
      this.selectedA[this.aptiLen].status=stat;
 }

addResoning(val,stat){
  this.selectedR[this.resLen].no=this.resLen+1;
  this.selectedR[this.resLen].opt=val;
  this.selectedR[this.resLen].status=stat;
}
addEnglish(val,stat){
  this.selectedE[this.engLen].no=this.engLen+1;
  this.selectedE[this.engLen].opt=val;
  this.selectedE[this.engLen].status=stat;
}
addGenral(val,stat){
  this.selectedG[this.genLen].no=this.genLen+1;
  this.selectedG[this.genLen].opt=val;
  this.selectedG[this.genLen].status=stat;
}
addComputer(val,stat){
  this.selectedC[this.compLen].no=this.compLen+1;
  this.selectedC[this.compLen].opt=val;
  this.selectedC[this.compLen].status=stat;
}

showResult(){
   this.rs.setAOption(this.selectedA);
   this.rs.setROption(this.selectedR);
   this.rs.setEOption(this.selectedE);
   this.rs.setGOption(this.selectedG);
   this.rs.setCOption(this.selectedC);
   this.router.navigate(['/examboard/exam/resultpage']);
 }

// incTimer(){
 //  this.min = this.min - 1;
 //  if(this.min == 0)
   //   {
//this.hr= this.hr - 1; this.ticks =60 ; this.min = 60;
      //   if(this.hr == 0)
      //   {
       //    this.stop = true;
///this.subscription.unsubscribe();
       //  }
//}
//}



getTimeRemaining(endtime) {
  var t = Date.parse(endtime) - Date.parse(new Date().toString());
  var seconds = Math.floor((t/ 1000) % 60);
  var minutes = Math.floor((t / 1000 / 60) % 60);
  var hours = Math.floor((t / (1000 * 60 * 60)) % 24);

  if(seconds==0 && minutes == 0 && hours == 0){
    this.stop = true;
    this.subscription.unsubscribe();
  }
  return {
    'total': t,
    'hours': hours,
    'minutes': minutes,
    'seconds': seconds
  };
}
updateClock() {
  var t = this.getTimeRemaining(this.endtime);

  this.hr = ('0' + t.hours).slice(-2).toString();
  this.min = ('0' + t.minutes).slice(-2).toString();
  this.sec = ('0' + t.seconds).slice(-2).toString();
}


timer(){
  let timer = TimerObservable.create(2000, 1000);
    this.subscription = timer.subscribe(t => { 
     //this.ticks = t%61 -1;
    //if(this.ticks == 0) { this.ticks =60;}//this.incTimer();this. this.updateClock();
      this.updateClock();
   });
}

//=============(tab setting)============//
setApti(){
  this.closeAll();
  if(this.aptiLen == this.aptituteQues.length-1){
    this.next = false;
   } else this.next = true;
  this.apti=true;
}
setRes(){
  this.closeAll();
  if(this.resLen == this.resoningQuestion.length-1){
    this.next = false;
   } else this.next = true;
  this.res=true;
}
setEng(){
  this.closeAll();
  if(this.engLen == this.englisgQuestion.length-1){
    this.next = false;
   } else this.next = true;

  this.eng=true;
}
setGen(){
  this.closeAll();
  if(this.genLen == this.genralQuestion.length-1){
    this.next = false;
   } else this.next = true;
  this.gen=true;
}
setComp(){
  this.closeAll();
  if(this.compLen == this.computerQuestion.length-1){
    this.next = false;
   } else this.next = true;
  this.comp=true;
}

closeAll(){
  this.apti=false;
  this.res=false;
  this.eng=false;
  this.gen=false;
  this.comp=false;
}
//=============(tab setting)============//
//=============(question jump)==========//
gotoApti(value){

  if(this.selectedA[this.aptiLen].no==null)
  {
    this.addApti(null,"visited");
  }
  this.aptiLen = value;

  if(this.aptiLen == this.aptituteQues.length-1){
    this.next = false;
   } else this.next = true;
}

gotoRes(value){
  if(this.selectedR[this.resLen].no==null)
   {
     this.addResoning(null,"visited");
   }
  this.resLen = value;
  if(this.resLen == this.resoningQuestion.length-1){
    this.next = false;
   } else this.next = true;
}
gotoEng(value){
   if(this.selectedE[this.engLen].no==null)
   {
     this.addEnglish(null,"visited");
   }
  this.engLen = value;

  if(this.engLen == this.englisgQuestion.length-1){
    this.next = false;
   } else this.next = true;
}
gotoGen(value){
    if(this.selectedG[this.genLen].no==null)
   {
     this.addGenral(null,"visited");
   }
  this.genLen = value;

  if(this.genLen == this.genralQuestion.length-1){
    this.next = false;
   } else this.next = true;
}
gotoComp(value){
   if(this.selectedC[this.compLen].no==null)
   {
     this.addComputer(null,"visited");
   }
  this.compLen = value;

  if(this.compLen == this.computerQuestion.length-1){
    this.next = false;
   } else this.next = true;
}
//=============(question jump)==========//
//=============(set review)=============//
review(){
  if(this.apti ) {
    this.addApti(null,"review");
   }
  else if(this.res){
    this.addResoning(null,"review");
  }
  else if(this.eng) {
    this.addEnglish(null,"review");
  }
  else if(this.gen){
    this.addGenral(null,"review");
  }
  else if(this.comp){
    this.addComputer(null,"review");
  }
}
//=============(set review)=============//
//=============(next question)=============//
incrementCount(){
   if(this.apti && this.aptiLen < this.aptituteQues.length-1) {
     if(this.aptiLen == this.aptituteQues.length-2){
      this.next = false;
     }
     if(this.value!=0){
      this.addApti(this.value,"answered");
      this.value=0;
     } else this.addApti(null,"visited");
     this.aptiLen ++;
    }
    else if(this.res && this.resLen < this.resoningQuestion.length-2){
      if(this.resLen == this.resoningQuestion.length-1){
        this.next = false;
       }
      if(this.value!=0){
        this.addResoning(this.value,"answered");
        this.value=0;
       } else this.addResoning(this.value,"visited");    
      this.resLen ++;
    }
    else if(this.eng && this.engLen < this.englisgQuestion.length-1) {
      if(this.engLen == this.englisgQuestion.length-2){
        this.next = false;
       }
      if(this.value!=0){
        this.addEnglish(this.value,"answered");
        this.value=0;
       } else this.addEnglish(this.value,"visited");
      this.engLen ++;
    }
    else if(this.gen && this.genLen < this.genralQuestion.length-1){
      if(this.genLen == this.genralQuestion.length-2){
        this.next = false;
       }
      if(this.value!=0){
        this.addGenral(this.value,"answered");
        this.value=0;
       } else this.addGenral(this.value,"visited");
      this.genLen ++;
    }
    else if(this.comp && this.compLen < this.computerQuestion.length-1){
      if(this.compLen == this.computerQuestion.length-2){
        this.next = false;
       }
      if(this.value!=0){
        this.addComputer(this.value,"answered");
        this.value=0;
       }
       else this.addComputer(this.value,"visited");
      this.compLen ++;
    }
}
//=============(next question)=============//

//=============(set value of option choosen)=============//
setValue(value){
  this.value=value;
  console.log("value = "+this.value);
}
//=============(set value of option choosen)=============//

//=============(saving last question option)=============//
save(){
  if(this.apti){
    if(this.value!=0){
      this.addApti(this.value,"answered");
      this.value=0;
    } else this.addApti(this.value,"visited"); 
  }
  else if(this.res){
    if(this.value!=0){
       this.addResoning(this.value,"answered");
       this.value=0;
    }else this.addResoning(this.value,"visited");
  }
  else if(this.eng){
    if(this.value!=0){
    this.addEnglish(this.value,"answered");
    this.value=0;
    }else this.addEnglish(this.value,"visited");
  }
  else if(this.gen){
    if(this.value!=0){
    console.log("value = "+this.value);
    this.addGenral(this.value,"answered");
    this.value=0;
    } else this.addGenral(this.value,"visited");
  }
  else if(this.comp){
    if(this.value!=0){
    this.addComputer(this.value,"answered");
    this.value=0;
    } else this.addComputer(this.value,"visited");
  }
}
//=============(saving last question option)=============//

//reset()
 reset(){
   if(this.apti){
     this.addApti(null,"visited");
   }
 }

}



