import { Component, OnInit } from '@angular/core';
import {QuestionService} from '../../../services/question.service';
import {ExamService} from '../../../services/exam.service';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {

//================quesion upload============ //
question;
option1;option2;option3;option4;
answer;
categories = [] ;
category:String;
chapters;
Aptitute=[];
Resoning = [];
English = [];
Genral = [];
Computer = [];
//===========(message)======================//
success:String = '';
error:String = '';

//==============(Exam question variables)==============//
examQuesStat:boolean;
aptitutelen=5;
resoninglen=10;
englen=3;
genlen=24;
exams=[];
cat;
examname:string; //exam name
examcat;  //exam category

//==============(Exam question variables)==============//
selected;
current;
currentExamName;
currentCatName;
newCatName='';
newExamName='';

ques_id:string;

  constructor(
    private qs:QuestionService,
    private es:ExamService
  )
   {
    this.resetMsg();
    this.qs.getCategory().subscribe(data => {
      if (data.success) {
          this.categories = data.categories;
      } else {
        this.categories = [''];
      }
    });  

    
  }

  ngOnInit() {
    this.resetMsg();
    this.examQuesStat = false;
    this.es.getExams().subscribe(data => {
      if (data.success) {
          this.exams = data.data;
          this.examname = data.data[0].examname;
          //getexam question 
    const ename = {
      examname : this.examname
     };
    this.es.getExamQuestion(ename).subscribe(data => {
      if (data.success) {
       data.data.forEach(elmt => {
         if(elmt.category=="aptitute") {this.Aptitute.push(elmt); 
         }
         else if(elmt.category=="resoning") {this.Resoning.push(elmt);}
         else if(elmt.category=="english") {this.English.push(elmt);}
         else if(elmt.category=="genralawareness") {this.Genral.push(elmt);}
         else if(elmt.category=="computer") {this.Computer.push(elmt);} 
       });
        } 
     });
       } else {
        this.exams = [''];
      }
    }); 
    
   this.selected ="resoning";
    
  }

//===========(question upload methods)====================//
addCategory(){
  this.resetMsg();
  let data = {
   category: this.cat
 };
 if(data.category == undefined || data.category == '')
     this.error = "please enter the category name";
 else{
  this.qs.addCategory(data).subscribe(data => {
    if (data.success) {
       this.success = data.msg;
       this.cat= '';
    } else {
      this.error = data.msg;
    }
  });  
 }   
}

  changeCategory(value){
    this.resetMsg();
    this.chapters = [''];
    this.category = value;
    let catdata = {
    category : this.category
    }  
  }

  changeCat(value){
    if(value == 'aptitute')
     this.selected = 'aptitute';

    else if(value == 'resoning')
    this.selected = 'resoning';

    else if(value == 'english')
    this.selected = 'english';

    else if(value == 'genralawareness')
    this.selected = 'genral';

    else if(value == 'computer')
    this.selected = 'computer';

  }
  //refreshing categories list
  refreshCategory(){
    this.resetMsg();
    this.qs.getCategory().subscribe(data => {
      if (data.success) {
          this.categories = data.categories;
      } else {
        this.categories = [''];
      }
    });  
  }
  getData(value){
    if(value == "Aptitute")
      return this.Aptitute;
    else if(value == "Resoning")  
           return this.Resoning;
    else if(value == "English")
       return this.English;
  }

//===========(Exam question upload methods)======================//
  enableStat(){
      this.examQuesStat = true;
      this.populateQuestionNumber(); //populating number of questions
  }
  disableStat(){
    this.examQuesStat = false;
    this.resetMsg();
  }

  changeExam(value){
      this.examname = value;
      console.log(value);
      this.emptyAllQuestion();
      this.getQuestions();
      this.populateQuestionNumber();
  }

  addExamQuestion(){ ///question adding method
      this.resetMsg();
      const examQuestion = {
        examname:this.examname,
        questions:{
        question : this.question.trim(),
        options : [this.option1.trim(),this.option2.trim(),this.option3.trim(),this.option4.trim()],
        answer:this.answer.trim(),
        category: this.examcat
      }
    }

    if(examQuestion.examname == undefined || examQuestion.examname == ''){
           this.error = "Please select the exam name";
           return false;
    }
    else if(this.examcat == undefined || this.examcat == ''){
            this.error = "Please select the category";
            return false;
        }
        else if(this.question == undefined || this.question == undefined){
          this.error = "please fill all the fileds ";
          return false;
        }
        else if(this.option1 == undefined || this.option2 == undefined || this.option3 == undefined||this.option4 == undefined){
                  this.error = "please fill all the Options fileds ";
                  return false;
        }
        else if(this.answer == undefined || this.answer == ''){
          this.error = "please fill  the Answer fileds ";
          return false;
        }  
    else{
      this.es.addExamQuestion(examQuestion).subscribe(data => {
       if (data.success) {
         this.success=data.msg;
         this.populateQuestionNumber();
         this.clearAll();
        } else {
         this.error = data.msg;
         return;
       }
     });
    }

  }
//get exam questions
getQuestions(){
  const ename = {
    examname : this.examname
   };
  this.es.getExamQuestion(ename).subscribe(data => {
    if (data.success) {
  
     data.data.forEach(elmt => {
       if(elmt.category=="aptitute") {this.Aptitute.push(elmt); 
       }
       else if(elmt.category=="resoning") {this.Resoning.push(elmt);}
       else if(elmt.category=="english") {this.English.push(elmt);}
       else if(elmt.category=="genralawareness") {this.Genral.push(elmt);}
       else if(elmt.category=="computer") {this.Computer.push(elmt);} 
     });
      } 
   });
}
//number of question uploaded to particular exam
    populateQuestionNumber(){
      const ename = {
        examname:this.examname
      }
      this.es.getExamQuestionStat(ename).subscribe(data => {
        if (data.success) {
           this.aptitutelen=data.data.aptitute;
           this.resoninglen = data.data.resoning;
           this.englen = data.data.english;
           this.genlen = data.data.genral;
        }
      }); 
    }
 //===========(Exam question upload methods)======================//
//===========(Adding exams )====================================//
addExam(){
  this.resetMsg();
  let data = {
   examname: this.examname
 }
 if(data.examname == undefined || data.examname == ''){
    this.error = "please enter the exam name";
    return false;
 }
else{
 this.es.addExam(data).subscribe(data => {
   if (data.success) {
      this.success = data.msg;
      this.examname= '';
   } else {
     this.error = data.msg;
   }
 }); 
}
}
//===========(Adding exams )====================================//

openEditor(index){
  if( this.selected == "aptitute"){
    this.current = this.Aptitute[index];
  } else if(this.selected == "resoning") { 
      this.current = this.Resoning[index];
    }else if(this.selected == 'english')
     { this.current = this.English[index];}
    else if(this.selected == 'genralawareness')
      { this.current = this.Genral[index]; }
    else if(this.selected == 'computer')
      { this.current = this.Computer[index];}      

      this.option1 = this.current.options[0];
      this.option2 = this.current.options[1];
      this.option3 = this.current.options[2];
      this.option4 = this.current.options[3];
}
//======(update question)==================//
updateQuestion(){
  this.resetMsg();
  let ques = {
    QID:this.current._id,
    examname : this.examname,
    questions :{
      question: this.current.question,
      answer : this.current.answer,
      category:this.current.category,
      options:[this.option1,this.option2,this.option3,this.option4] 
      }
  }
  this.es.updateQues(ques).subscribe(data => {
    if (data.success) {
       this.success = data.msg;
       this.clearAll();
    }
    else{
        this.error = data.msg; 
    }
  }); 
}
//======(update question)==================//

setCat(cat){
 this.currentCatName = cat;
 this.newCatName = cat;
}
setExamName(exam){
 this.currentExamName = exam;
 this.newExamName = exam;
}

//update cat
updateCat(){
  this.resetMsg();
  let newCat={
    oldCat : this.currentCatName,
    newCat : this.newCatName
  }
  this.qs.updateCategory(newCat).subscribe(data => {
    if (data.success) {
      this.refreshCategory();
      this.success = data.msg;
    }
    else{
        this.error = data.msg; 
    }
  }); 
}
updateExam(){
  this.resetMsg();
  let newExam={
    oldExamName: this.currentExamName,
    newExamName : this.newExamName
  }
  this.es.updateExam(newExam).subscribe(data => {
    if (data.success) {
       this.currentCatName = '';
       this.success = data.msg;
    }
    else{
        this.error = data.msg; 
    }
  }); 
}
//deleteing category
deleteCat(){
  this.resetMsg();
    let cat = {
      category : this.currentCatName
    }
    this.qs.deleteCategory(cat).subscribe(data => {
      if (data.success) {
         this.currentExamName = '';
         this.success = data.msg;
         this.refreshCategory();
      }
      else{
          this.error = data.msg; 
      }
    }); 
}
//deleteing exam
deleteExam(){
  this.resetMsg();
    let examname = {
      examname : this.currentExamName
    }
    this.es.deleteExam(examname).subscribe(data => {
      if (data.success) {
         this.currentExamName = '';
         this.success = data.msg;
      }
      else{
          this.error = data.msg; 
      }
    }); 
}
//delete questions
setID(val){
 this.ques_id = val;
}

deleteQuestion(){
  let examQues = {
    examname: this.examname,
    QID : this.ques_id
  }
  this.es.deleteExamQuestion(examQues).subscribe(data => {
    if (data.success) {
       this.ques_id= '';
       this.success = data.msg;
    }
    else{
        this.error = data.msg; 
    }
  }); 
}
//delete questions
  clearAll(){
    this.question = '';
    this.option1 = '';
    this.option2 = '';
    this.option3 = '';
    this.option4 = '';
    this.answer = '';
    }

    resetMsg(){
      this.error = '';
      this.success = '';
    }

    setError(msg){
          this.error = msg;
    }
    setSuccess(msg){
      this.success = msg;
    }

    emptyAllQuestion(){
      this.Aptitute.splice(0, this.Aptitute.length);
      this.Resoning.splice(0,this.Resoning.length);
      this.English.splice(0,this.English.length);
      this.Genral.splice(0,this.Genral.length);
      this.Computer.splice(0,this.Computer.length);
    }
}



